@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;500&display=swap);

:root {
  --robot:'Roboto', sans-serif;
  --cool-black: hsl(0, 12%, 13%);
  --white: rgb(255, 255, 255);
  --hover-red: rgb(228, 111, 111);
  --red: rgb(179, 32, 32);

  --fs-sm: 1rem;
  --fs-reg: 2rem;
  --fs-lg: 3.5rem;

  --fw-bold: 500;
  --fw-light: 300;

}

* {
  margin: 0;
  padding: 0;
}
*,
*::before,
*::after {
    box-sizing: border-box;
}
html {
  position: relative;
}

html,
body {
  height: 100%;
}
body {
  font-family: 'Roboto', sans-serif;
  font-family: var(--robot);
  color: hsl(0, 12%, 13%);
  color: var(--cool-black);
  background: blueviolet;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  text-align: center;
  position: relative;
}

.card {
  width: 35rem;
  height: 80vh;
  display: block;
  border: 1px solid black; 
  background: rgb(255, 255, 255); 
  background: var(--white);
  text-align: center;
  position: absolute;
  margin: 6.0rem  0;
}
.c-header {
  height: auto;
  margin: 2.5rem auto;
}
.pic {
  max-width: 100%;
  border-radius:50%;
}
.c-body {
  height: auto;
  margin: 2rem auto;
}

.greeting {
  font-size: 3.5rem;
  font-size: var(--fs-lg);
  margin-bottom: .75rem;
}
.message {
  font-size: 2rem;
  font-size: var(--fs-reg);
  font-weight: 300;
  font-weight: var(--fw-light);
  margin-bottom: 1rem;
}
.btn {
  height: 4rem;
  width: 7rem;
  border: none;
  border-radius: .9375rem;
  font-size: 1.1rem;
  font-weight: 500;
  font-weight: var(--fw-bold);
}
.my-name {
  margin-right: 1.5rem;
}
.likes {
  text-align: center;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-content: center;
          align-content: center;
  margin-bottom: 1.5rem;
}

.fas {
  
  color: rgb(179, 32, 32);
  
  color: var(--red);
}
.count,
.fas,
.fab {
  font-size: 2.5rem;
}
.fab {
  font-size: 2.5rem;
}
.fab {
  color: rgb(33, 89, 219);
}

.fa-twitter {
  margin-right: .35rem;
}


.fa-github-square {
  margin-left: .35rem;
}
@media screen and (max-width: 21.125) {
  .card {
    max-width: 100%;
    }
}
